.recommended {
  &__products {
    @include pageGrid();

    row-gap: 40px;
  }

  &__product {
    grid-column: 1 / -1;

    @include onDesktop() {
      grid-column: span 4;
    }
  }
}

.product {
  &__photo {
    transition: transform $effectDuration;

    &:hover {
      transform: scale(110%);
    }
  }
}
