.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #f9f9f9;
  font-weight: 700;

  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;

    margin-bottom: 32px;

    @include onTablet() {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    display: block;
    width: fit-content;
    margin-bottom: 16px;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: transform $effectDuration;

    &:hover {
      transform: scale(110%);
    }
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 3px;
    border-bottom: 1px solid $main-text-color;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: transform $effectDuration;

    &:hover {
      transform: scale(110%);
    }
  }
}
